<template>
  <div id="app">
    <h2>
      {{ `${date.getMonth()}월 ${date.getDate()}일 의 디미밥` }}
    </h2>
    <div
      :class="{
        'meal': true,
        'meal__now': index === nextMealKind
      }"
      :key="kind"
      v-for="(kind, index) in list"
    >
      {{ kind }}
      {{ indexedMeal[index] ? indexedMeal[index] : '급식 정보를 불러오고 있습니다' }}
    </div>
    <div
      class="footer"
    >
      <a href="">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"/></svg>
        <span>18HD 엄서훈</span>
      </a>
      <br>
      <span>API by dimigoIN</span>
    </div>
  </div>
</template>

<script>
import { list } from './data.json'

export default {
  name: 'app',

  data () {
    return {
      date: new Date(),
      meal: {},
      list
    }
  },

  computed: {
    indexedMeal () {
      return [this.meal.breakfast,
        this.meal.lunch,
        this.meal.dinner]
    },
    nextMealKind () {
      const time = new Date()
      if (time.getHours() < 9) return 0
      else if (time.getHours() < 13) return 1
      else return 2
    }
  },

  created() {
    const date = new Date().toISOString().slice(0,10).replace(/-/g,"")
    this.$api.get(`https://dev-api.dimigo.in/dimibobs/${date}`).then(result => {
      this.meal = result.data
    })
  }
}
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css?family=Do+Hyeon');

#app {
  font-family: 'Do Hyeon', sans-serif;
  font-size: 120%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2 {
  font-weight: normal;
}

.meal {
  color: #606060;
  &__now {
    color: black !important;
    font-size: 130%;
  }
}

.footer {
  padding-top: 20px;
}

a {
  color: black;
  text-decoration: none;
}
</style>



